import React from 'react'
import Navbar from './Navbar'

export default function NewYear(props) {
    return (
        <div className='page'>
            <Navbar changePage={props.changePage} toggleDropdown={props.toggleDropdown} />
            <br />
            <br />
            <h1 className='title'>New Year, New Blog!</h1>
            <div>Sunday, January 6th, 2019 || 11:58PM CST</div>
        </div>
    )
}