import React from 'react'
import Navbar from './Navbar'

export default function SetState(props) {
    return (
        <div className='page'>
            <Navbar changePage={props.changePage} toggleDropdown={props.toggleDropdown} />
            <br />
            <br />
            <h1 className='title'>Changing State in React Using setState()</h1>
            <div>Wednesday, January 23rd, 2019 || 10:02AM CST</div>
        </div>
    )
}